import React, { useState, useEffect } from 'react'
import styled from 'styled-components'
import moment from 'moment-timezone'
import Confetti from 'react-confetti'

import DistanceBar from './components/DistanceBar'

const StyledHeader = styled.div`
  width: 80%;
  margin: 0 auto;
`

const StyledTitle = styled.h1`
  font-family: 'Courier New', Courier, monospace;
  opacity: 0.2;
  font-weight: 500;
  text-align: center;
`

const StyledBody = styled.div`
  max-width: 900px;
  margin: 0 auto;
  padding: 0 10px;
`

export default () => {
  console.log('paint')
  const width = window.innerWidth
  const height = window.innerHeight

  const [celebrate, setCelebrate] = useState(false)
  const [zone, setZone] = useState(moment.tz.guess())
  const [time, setTime] = useState(moment().format('MMM Do YYYY, h:mm:ss a'))

  useEffect(
    () => {
      const time = moment().format('MMM Do YYYY, h:mm:ss a')
      setTime(time)
    },
    [time]
  )

  return (
    <>
      {celebrate ? <Confetti width={width} height={height} /> : null}

      <StyledHeader>
        <StyledTitle>Are We There Yet?</StyledTitle>
        {/* <p>Current time: {time} </p>
        <p>Timezone: {zone}</p> */}
      </StyledHeader>
      <StyledBody>
        <DistanceBar setCelebrate={setCelebrate} />
      </StyledBody>
    </>
  )
}
